@charset "UTF-8";
/*
** A Modern CSS Reset
*/
*, *::before, *::after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, h5, h6, p, ul, ol, figure, blockquote, dl, dd {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  line-height: 1.5;
  overflow-x: hidden;
  text-rendering: optimizeSpeed;
}

ul, ol {
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

table {
  border-collapse: collapse;
}

th {
  font-weight: normal;
}

/*
** common 
*/
body {
  box-sizing: border-box;
  color: #333;
  font-family: 'Ubuntu', "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  letter-spacing: .1em;
  overflow-x: hidden;
}

a {
  color: #333;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

main {
  display: block;
}

img {
  width: 100%;
}

.wrapper {
  position: relative;
  height: 100%;
  min-height: 360px;
  max-width: 1920px;
  overflow-x: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.wrapper::-webkit-scrollbar {
  display: none;
}

.heading {
  display: flex;
  align-items: center;
  justify-content: left;
  color: #3A405A;
}

@media screen and (min-width: 62em) {
  .heading {
    justify-content: center;
  }
}

.icon {
  width: 2rem;
  height: 2rem;
}

.section-title {
  margin: 0 0 0 1rem;
  font-size: 2rem;
}

@media screen and (min-width: 62em) {
  .section-title {
    font-size: 2.4rem;
  }
}

.content {
  margin: 2rem 0 8rem 0;
}

@media screen and (min-width: 62em) {
  .content {
    max-width: 80%;
    margin: 4rem auto 20rem;
    font-size: 1.2rem;
  }
}

.js-fade-in {
  position: static;
  opacity: 0;
  transform: translate(0, 50px);
  transition: all 1.5s;
}

.js-fade-in.fade-in {
  opacity: 1;
  transform: translate(0, 0);
}

/*
** header
*/
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(174, 197, 235, 0.9);
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  z-index: 99;
}

.sidebar::-webkit-scrollbar {
  display: none;
}

@media screen and (min-width: 48em) {
  .sidebar {
    padding: 56px 40px;
    height: 100%;
    width: 256px;
    background-color: #AEC5EB;
  }
}

.sidebar__logo {
  padding: 8px 0;
  font-family: 'Josefin Sans';
  font-size: 1.6rem;
  font-weight: normal;
  text-align: center;
  transition: background-color 0.3s linear 0s;
}

.sidebar__logo:hover, .sidebar__logo:focus {
  background-color: #3A405A;
}

.sidebar__logo:hover .sidebar__logo-title, .sidebar__logo:focus .sidebar__logo-title {
  color: #F9DEC9;
}

@media screen and (min-width: 48em) {
  .sidebar__logo {
    border: 2px solid #3A405A;
  }
}

.side-menu {
  display: none;
  font-size: 1.4rem;
  text-align: center;
}

.side-menu .fa-caret-right {
  color: #F9DEC9;
  opacity: 0;
}

@media screen and (min-width: 48em) {
  .side-menu {
    display: block;
    margin: 3rem 0 0 0;
    border: none;
    text-align: left;
  }
}

.side-menu__item {
  padding: .4em 0;
  border-top: 1px solid #3A405A;
}

@media screen and (min-width: 48em) {
  .side-menu__item {
    padding: .6em 0;
    border: none;
    overflow-x: hidden;
    transition: padding 0.3s linear 0s;
  }
}

.side-menu__item .fa-caret-right {
  display: none;
}

@media screen and (min-width: 48em) {
  .side-menu__item .fa-caret-right {
    display: inline-block;
  }
}

.side-menu__item > a {
  display: inline-block;
  width: 100%;
}

@media screen and (min-width: 48em) {
  .side-menu__item > a {
    display: inline;
  }
}

@media screen and (min-width: 48em) {
  .side-menu__item:hover, .side-menu__item:focus {
    padding: .6em .2em .6em .6em;
  }
  .side-menu__item:hover .fa-caret-right, .side-menu__item:focus .fa-caret-right {
    opacity: 1;
  }
}

/*
** ハンバーガーメニュー
*/
.btn {
  position: fixed;
  top: 14px;
  right: 12px;
  width: 1.6em;
  height: 1.6em;
  cursor: pointer;
}

@media screen and (min-width: 48em) {
  .btn {
    display: none;
  }
}

.btn__text {
  display: block;
  margin-top: 1.5em;
  font-weight: bold;
  text-transform: uppercase;
}

.btn__menu {
  appearance: none;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 2px;
  background: #26344a;
  margin: 0;
  padding: 0;
  border: none;
  transition: all 0.5s;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.btn__menu::before {
  display: block;
  content: "";
  position: absolute;
  top: -8px;
  left: 0;
  width: 100%;
  height: 100%;
  background: #26344a;
  transition: all 0.5s;
}

.btn__menu::after {
  display: block;
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 100%;
  height: 100%;
  background: #26344a;
  transition: all 0.5s;
}

.btn__menu.active {
  background: transparent;
}

.btn__menu.active::before {
  top: 0;
  transform: rotate(-405deg);
}

.btn__menu.active::after {
  bottom: 0;
  transform: rotate(405deg);
}

/*
** main
*/
.main {
  position: relative;
  min-height: 360px;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.main::-webkit-scrollbar {
  display: none;
}

.main__wrap {
  margin: 1rem;
  padding: 2rem;
  background: linear-gradient(to right, transparent 5%, rgba(249, 222, 201, 0.4) 95%, transparent);
  border-radius: .4rem;
}

@media screen and (min-width: 48em) {
  .main__wrap {
    margin: 1rem 2rem 1rem 4rem;
  }
}

@media screen and (min-width: 62em) {
  .main__wrap {
    margin: 4rem 2rem 1rem 4rem;
    padding: 8rem 2rem 2rem 2rem;
  }
}

.main::after {
  display: block;
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  margin: 54px 0 0 0;
  width: 100vw;
  height: 100vh;
  background-image: url("../img/main-sp.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -9;
}

@media screen and (min-width: 48em) {
  .main::after {
    margin: 0 0 0 256px;
    background-image: url("../img/main-pc.jpg");
    background-position: center left;
  }
}

@media screen and (min-width: 48em) {
  .main {
    margin: 0 0 0 256px;
  }
}

/*
** top
*/
.main__top {
  position: relative;
  height: 100vh;
  min-height: 360px;
}

.title-box {
  position: absolute;
  top: 46%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  text-align: center;
}

@media screen and (min-width: 48em) {
  .title-box {
    width: auto;
  }
}

.title-box__title {
  display: inline-block;
  font-family: 'Josefin Sans';
  font-size: 3.2rem;
}

@media screen and (min-width: 62em) {
  .title-box__title {
    font-size: 4rem;
  }
}

.title-box__subtitle {
  margin-top: 1em;
  font-size: 1.2rem;
}

@media screen and (min-width: 36em) {
  .title-box__subtitle {
    font-size: 1.6rem;
  }
}

@media screen and (min-width: 62em) {
  .title-box__subtitle {
    font-size: 2rem;
  }
}

/*
** profile
*/
/*
** skills
*/
@media screen and (min-width: 62em) {
  .skills .skills__content {
    display: flex;
    justify-content: center;
    font-size: 1.3rem;
  }
}

.skills .list {
  list-style: disc inside;
}

@media screen and (min-width: 62em) {
  .skills .list__item:not(:last-child) {
    padding: 0 0 .5rem 0;
  }
}

/*
** works
*/
@media screen and (min-width: 62em) {
  .works__intro {
    margin: 0 auto;
  }
}

@media screen and (min-width: 62em) {
  .works .list {
    display: flex;
    flex-wrap: wrap;
  }
}

.works .list__item {
  margin: 4rem 0 0 0;
}

@media screen and (min-width: 62em) {
  .works .list__item {
    width: 50%;
  }
}

.js-modal-open {
  margin: 0 auto;
  width: 80%;
  height: 100%;
  cursor: pointer;
}

.js-modal-close {
  position: absolute;
  top: calc(54px + .2rem);
  right: calc((100% - 80%) * 0.5);
  transform: translate(-50%, 0);
  color: #F9DEC9;
}

@media screen and (min-width: 48em) {
  .js-modal-close {
    right: calc((100% - 64%) * 0.5);
  }
}

.thumbnail {
  margin: 0 auto;
  text-align: center;
}

.thumbnail__link {
  display: inline-block;
  margin: .6rem 0 0 0;
  color: #3A405A;
  text-decoration: underline;
  transition: all 0.3s;
}

.thumbnail__link:hover, .thumbnail__link:focus {
  opacity: .8;
}

.thumbnail .disable {
  pointer-events: none;
  text-decoration: line-through;
}

.modal {
  display: none;
  position: fixed;
  top: 54px;
  left: 0;
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 48em) {
  .modal {
    z-index: 999;
  }
}

.modal__content {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  overflow-y: scroll;
}

.modal__img {
  position: absolute;
  top: 54px;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 2rem;
  width: 80%;
}

@media screen and (min-width: 48em) {
  .modal__img {
    max-width: 992px;
    width: 64%;
  }
}

/*
** contact
*/
.contact__content {
  display: flex;
  justify-content: center;
}

.contact input[type="text"], .contact input[type="email"] {
  width: 100%;
}

@media screen and (min-width: 62em) {
  .contact input[type="text"], .contact input[type="email"] {
    padding: .2rem;
    font-size: 1.2rem;
  }
}

.contact input[type="submit"] {
  width: 64px;
  height: 40px;
}

@media screen and (min-width: 62em) {
  .contact input[type="submit"] {
    font-size: 1.2rem;
  }
}

.contact textarea {
  width: 100%;
}

@media screen and (min-width: 62em) {
  .contact textarea {
    width: 640px;
    font-size: 1.2rem;
  }
}

/*
** footer
*/
.footer {
  width: 100%;
  background-color: #AEC5EB;
  line-height: 3rem;
  text-align: center;
}

@media screen and (min-width: 48em) {
  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 256px;
    z-index: 99;
  }
}
